//——————————————————————————————————————————————————————————————————————————————
// Common Design: Layout
//——————————————————————————————————————————————————————————————————————————————

html {
  height: 100%;

  @include desktop {
    padding: 0 $cd-site-padding;
  }
}

body {
  background: $cd-site-bg-color;
  min-height: 100%;

  //&:not(.user-logged-in) {
  //  @supports (display: grid) {
  //    display: grid;
  //    grid-template-rows: 1fr auto;
  //  }
  //}
}

.cd-layout-container {
  background: $cd-white;
  margin: 0 auto;
  max-width: $cd-max-body-width;
  min-height: 100%;
  padding: 0;

  // helper for @support testing. Enable background for colours.
  //background: rgba(255, 0, 0, 0.1);

  // sass-lint:disable no-empty-rulesets
  @supports (display: flex) {
    //background: rgba(0, 255, 0, 0.1);
  }

  // Grid is used to fix the footer to the bottom of the screen, regardless of page min-height.
  @supports (display: grid) {
    //background: rgba(0, 0, 255, 0.1);
    @include tablet {
      //display: grid;
      //grid-template-rows: 95px 1fr auto; // Height of header.
    }
  }
}

.cd-container {
  @include clearfix;
  box-sizing: border-box;
  padding: 0 $cd-container-padding;
  margin: 0 auto;
  width: 100%;
  max-width: $cd-max-width;

  @include tablet {
    padding: 0 $cd-container-padding-tablet;
  }

  @include xlarge {
    padding: 0 $cd-container-padding-xlarge;
  }
}

.cd-container[role='main'] {
  padding-bottom: 2rem;

  @include desktop {
    padding-bottom: 4rem;
  }
}

// with sidebar pattern
.cd-layout {
  //padding-top: 2rem;

  @include tablet {
    //padding-top: 3rem;
    display: flex;
    flex-wrap: wrap;
  }

  // Assumes frontpage has hero as first element in header field.
  .path-frontpage:not(.user-logged-in) & {
    padding-top: 0;
  }
}

.cd-sidebar {
  flex-basis: 285px;
  flex-grow: 1;
  //background: rgba(yellow, 0.2);
}

.cd-main {
  flex-basis: 0;
  flex-grow: 999;
  min-width: 50%;
  //background: rgba(red, 0.2);
}

.cd-layout-container main.cd-container {
  @extend .cd-layout;
}

.cd-layout-container main.cd-container .cd-layout-sidebar-first {
  @extend .cd-sidebar;
}

.cd-layout-container main.cd-container .cd-layout-main-content {
  @extend .cd-main;
}

section.cd-layout-two-col {
  @extend .cd-layout;

  .field--name-field-content {
    @extend .cd-main;
  }

  .field--name-field-sidebar {
    @extend .cd-sidebar;
  }
}

// Override common_design region layout rule.
.cd-layout-with-sidebar .cd-layout-content {
  @include tablet {
    margin-right: 0;
  }
}

.cd-layout-with-section-sidebar .cd-layout-main-content,
.cd-layout-two-col .cd-layout-main-content,
.cd-layout-with-region-sidebar .cd-layout-content {
  @include tablet {
    margin-right: 3rem;
  }
}
